export class KeyboardManager {
    constructor(app) {
        this.app = app;
    }

    setupKeyboardShortcuts() {
        document.addEventListener('keydown', (e) => {
            if (e.target.tagName === 'INPUT' || e.target.tagName === 'TEXTAREA') {
                return;
            }

            if (e.ctrlKey || e.metaKey) {
                this.handleCtrlShortcuts(e);
            } else {
                this.handleRegularShortcuts(e);
            }
        });
    }

    handleCtrlShortcuts(e) {
        switch(e.key.toLowerCase()) {
            case 'o':
                e.preventDefault();
                document.getElementById('fileInput')?.click();
                break;
            case 's':
                e.preventDefault();
                this.app.controls.downloadJSON();
                break;
            case 'e':
                e.preventDefault();
                this.app.controls.downloadImage();
                break;
            case 'l':
                e.preventDefault();
                this.app.controls.addNewLane();
                break;
            case 'n':
                e.preventDefault();
                this.app.controls.addNewNode();
                break;
            case '+':
            case '=':
                e.preventDefault();
                this.app.renderer.zoom(1.2);
                break;
            case '-':
            case '_':
                e.preventDefault();
                this.app.renderer.zoom(0.8);
                break;
            case '0':
                e.preventDefault();
                this.app.renderer.fitToScreen();
                break;
        }
    }

    handleRegularShortcuts(e) {
        switch(e.key.toLowerCase()) {
            case 'c':
                if (!e.target.closest('input, textarea')) {
                    this.app.controls.toggleConnectMode();
                }
                break;
            case 'h':
                this.showHelp();
                break;
            case 'escape':
                this.app.editor.cancelConnection();
                if (this.app.controls.connectMode) {
                    this.app.controls.toggleConnectMode();
                }
                break;
        }
    }

    showHelp() {
        const helpText = `
ðŸŠ Swim Lane Diagram - Keyboard Shortcuts

File Operations:
â€¢ Ctrl+O: Open file
â€¢ Ctrl+S: Save as JSON
â€¢ Ctrl+E: Export as image

Editing:
â€¢ Ctrl+L: Add new lane
â€¢ Ctrl+N: Add new node
â€¢ C: Toggle connection mode
â€¢ Delete: Delete selected node
â€¢ Ctrl+Z: Undo
â€¢ Ctrl+Y: Redo

View:
â€¢ Ctrl+Plus: Zoom in
â€¢ Ctrl+Minus: Zoom out
â€¢ Ctrl+0: Fit to screen

Other:
â€¢ H: Show this help
â€¢ Escape: Cancel current operation
â€¢ Double-click node: Edit node
â€¢ Double-click lane: Edit lane
â€¢ Drag nodes to move them
        `;
        
        alert(helpText);
    }
}